'use strict';

/**
* Integer binary logarithm (base two).
*
* @module @stdlib/fastmath/special/uint32-log2
*
* @example
* var log2Uint32 = require( '@stdlib/fastmath/special/uint32-log2' );
*
* var v = log2Uint32( 4 >>> 0 );
* // returns 2
*
* v = log2Uint32( 8 >>> 0 );
* // returns 3
*
* v = log2Uint32( 9 >>> 0 );
* // returns 3
*/

// MODULES //
var log2Uint32 = __var_0__;


// EXPORTS //
export default log2Uint32;
